<script>
import { Toast } from 'vant';
import axios from 'axios'

const options = {
    data() {
        return {
            username: "", // 显示在头像下面的用户名
            flag: {
                islogin: false,
            },
            users: [],
            show: { // 弹出框是否显示标志
                login: false,
                register: false,
                protocol: false,
                leaveMsg: false
            },
            active: { // 底部激活索引
                footer: ""
            },
            login: { // 登录表单
                username: "",
                password: "",
            },
            register: { // 注册表单
                username: "",
                password: "",
                email: "",
            },
            message:"" // 用户留言
        }
    },
    methods: {
        goItemAdmin() {
            if (localStorage.getItem("user") != 'aaa' && localStorage.getItem("user") != 'admin') {
                Toast("您无权限操作!");
                return;
            }
            this.$router.push("/manage");
        },
        goOrderAdmin(){
            if (localStorage.getItem("user") != 'aaa' && localStorage.getItem("user") != 'admin') {
                Toast("您无权限操作!");
                return;
            }
            this.$router.push("/ordermanage")
        },
        goProtocol() {
            console.log("protocol...")
            this.show.protocol = true;
        },
        goAddress() {
            if (localStorage.getItem("user")) {
                this.$router.push("/address");
                return;
            }
            Toast("您尚未登录!");
        },
        goOrders() {
            if (localStorage.getItem("user")) {
                this.$router.push("/orders");
                return;
            }
            Toast("您尚未登录!");
        },
        goCollections(){
            Toast("功能开发中...");
        },
        goLeaveMsg() {
            if (localStorage.getItem("user")) {
                this.show.leaveMsg = true;
                return;
            }
            Toast("您尚未登录!");
        },
        showLogin() {
            this.show.login = true;
        },
        showRegister() {
            this.show.register = true;
        },
        submitLogin() {
            const user = {
                username: this.login.username,
                password: this.login.password,
            };
            axios.post("/users/login", user).then((resp) => {
                Toast(resp.data.msg);
                if (resp.data.data) {
                    // 登录成功
                    this.flag.islogin = true;
                    this.username = this.login.username;
                    localStorage.setItem("user", this.login.username);
                    this.login = {
                        username: "",
                        password: ""
                    }
                    this.show.login = false;
                }
            })
        },
        submitRegister() {
            const user = {
                username: this.register.username,
                password: this.register.password,
                email: this.register.email
            };
            axios.post("/users/register", user).then((resp) => {
                Toast(resp.data.msg);
                if (resp.data.data) {
                    // 注册成功
                    this.register = {
                        username: "",
                        password: "",
                        email: ""
                    };
                    this.show.register = false;
                }
            })
        },
        onSubmit() {

        },
        submitLeaveMsg(){
            /**
             * 提交用户留言
             */
            console.log(this.message);
            Toast("留言成功, 您的建议是我们前进的动力")
        },
        logout() {
            axios.post("/users/logout").then((resp) => {
                Toast(resp.data.msg);
                localStorage.removeItem("user");
                this.username = "";
                this.flag.islogin = false;
            });
        },
        islogin() {
            axios.get("/users/islogin").then((resp) => {
                if (!resp.data.data) {
                    this.flag.islogin = false;
                    this.username = "未登录";
                    localStorage.removeItem("user");
                } else {
                    this.flag.islogin = true;
                }
            });
        }
    },
    mounted() {
        console.log("person...");
        // 每次页面加载完毕之后判断用户是否登录, 确保前后端登录状态同步
        this.islogin();
        if (localStorage.getItem("user")) {
            this.flag.islogin = true;
            this.username = localStorage.getItem("user");
        }
    },
}
export default options;
</script>